import './App.css';
import { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

import BillsView from './containers/Bills/BillsView';
import VotesView from './containers/Votes/VotesView';
import HouseView from './containers/House/HouseView';
import SenateView from './containers/Senate/SenateView';
import { HashRouter, Route, Routes} from 'react-router-dom';

class App extends Component {
  	render() {
		return (
	  		<div className="App">
		  		<HashRouter basename="/">
					<Routes>
						<Route path="/senate" element={<SenateView />} />
						<Route path="/house" element={<HouseView />} />
						<Route path="/bills" element={<BillsView />} />
						<Route path="/votes" element={<VotesView />} />
						<Route path="" exact element={<SenateView />} />
					</Routes>
				</HashRouter>
				<div className="footer">
		  			<span>Data sourced from ProPublica</span>
				</div>
	  		</div>

		);
  	}
}

export default App;
